import {
    Component,
    OnInit,
    Input,
    ViewChild,
    AfterViewInit,
    ElementRef,
    NgZone,
    EventEmitter,
    Output
} from "@angular/core";
import { StoreService } from "../../../shared/services/store.service";
import { FormBuilder, FormGroup, Validators, FormArray } from "@angular/forms";
import { Router } from "@angular/router";
import { ModalComponent } from "../../../shared/components/modal/modal.component";
import {
    ProduitService,
    CouponService,
    TransporteurService,
    EmailService,
    CategorieService,
    MagasinService
} from "../../../api-shop";
import {
    CommandeService,
    MoyensService,
    MagasinsService
} from "../../../api-paiement";
import "react-dates/initialize";
import "react-dates/lib/css/_datepicker.css";
import {
    SingleDatePicker,
    DateRangePicker,
    isInclusivelyAfterDay
} from "react-dates";
import { ReactCptComponent } from "../../../shared/components/react-cpt/react-cpt.component";
import { UserService, GroupeService } from "../../../api-member";
import { getCountryName } from "../../../shared/utils/utils";
declare var moment: any;

@Component({
    selector: "app-shop-actionbar",
    templateUrl: "./actionbar.component.html",
    styleUrls: ["./actionbar.component.scss"]
})
export class ActionBarComponent implements OnInit, AfterViewInit {
    @ViewChild("modalPromo") modalPromo: ModalComponent;
    @ViewChild("modalUser") modalUser: ModalComponent;
    @ViewChild("modalLinkCategorie") modalLinkCategorie: ModalComponent;
    @ViewChild("modalLinkGroupe") modalLinkGroupe: ModalComponent;
    @ViewChild("modalExportCategorie") modalExportCategorie: ModalComponent;
    @ViewChild(ReactCptComponent) rcptDate: ReactCptComponent;
    @ViewChild("modalExportCommande") modalExportCommande: ModalComponent;
    @Output() onReRender: EventEmitter<any> = new EventEmitter();

    @Input() action;
    @Input() param;
    @Input() domain;
    @Input() total;
    @Input() path;
    promoForm: FormGroup;
    userForm: FormGroup;
    dateForm: FormGroup;
    originalPrice = 0;
    isPercentage = 1;
    isDeleting = false;
    startDate = moment();
    endDate = moment();
    emailPattern = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    telPattern = /(?:(?:\+|00)33[\s.-]{0,3}(?:\(0\)[\s.-]{0,3})?|0)[1-9](?:(?:[\s.-]?\d{2}){4}|\d{2}(?:[\s.-]?\d{3}){2})$/;

    genreChoices = [
        { name: "Mr", id: 0 },
        { name: "Mme", id: 1 },
        { name: "", id: 2 }
    ];
    genreChoix = {};

    showCategorieDropdown = false;
    selectedCategorie = null;

    categorieDropdownPlaceholder = "Sélectionner";
    categories = [];
    isCategorieLoaded = false;

    showGroupeDropdown = false;
    selectedGroupe = null;
    groupeDropdownPlaceholder = "Sélectionner";
    groupes = [];
    isGroupeLoaded = false;

    modules = [
        { name: "tva", field: "libelle", isEditable: true },
        { name: "categorie", field: "title", isEditable: true },
        { name: "fabricant", field: "nom", field2: "logo", isEditable: true },
        {
            name: "produit",
            field: "titre",
            field2: "imageUrl",
            isEditable: false
        },
        { name: "fournisseur", field: "nom", isEditable: true },
        { name: "tag", field: "nom", isEditable: true },
        {
            name: "messagestock",
            field: "message",
            field2: "logo",
            isEditable: true
        },
        {
            name: "messagestockcritique",
            field: "message",
            field2: "logo",
            isEditable: true
        },
        {
            name: "messagehorsstock",
            field: "message",
            field2: "logo",
            isEditable: true
        },
        {
            name: "transporteur",
            field: "nom",
            field2: "logo",
            isEditable: false
        },
        { name: "groupe", field: "nom", isEditable: true }
    ];

    storeItems = {
        categorie: [
            {
                id: 2,
                title: "Cartes",
                lvl: 1,
                active: true,
                ordre: 0,
                children: []
            }
        ]
    };

    toggleDropdown(name) {
        this[name] = !this[name];
    }

    displayDropdownCss(field) {
        return {
            "dropdown-error": field.invalid
        };
    }

    byValue(item1, item2) {
        if (!item2) {
            return false;
        }
        return item1.id == item2.id;
    }

    displayFieldCss(form, field: string) {
        if (!form.get(field)) {
            console.log("field not found :", field);
            return null;
        }

        return {
            "we-is-invalid": !form.get(field).valid
        };
    }

    component = DateRangePicker;
    focused: any;
    focusedInput: any;
    falseFunc() {
        return false;
    }

    props = {
        startDatePlaceholderText: "Date de début",
        endDatePlaceholderText: "Date de fin",
        hideKeyboardShortcutsPanel: true,
        startDate: this.startDate,
        minimumNights: 0, // permet la selection d'un seul jour
        isOutsideRange: day => {
            if (this.props.focusedInput == "endDate") {
                // End date is not after today and not before start date and not after 31 days
                return (
                    day.isAfter(moment(new Date())) ||
                    day.isBefore(this.startDate, "days") ||
                    day.isAfter(
                        this.startDate && this.startDate.clone().add(31, "days")
                    )
                );
            }
            if (this.props.focusedInput == "startDate") {
                // Start date is not after today
                return day.isAfter(moment(new Date()));
            }
            return false;
        },

        startDateId: "your_unique_start_date_id",
        endDate: this.endDate,
        endDateId: "your_unique_end_date_id",
        onDatesChange: ({ startDate, endDate }) =>
            this.datesChanged({ startDate, endDate }), // PropTypes.func.isRequired,
        focusedInput: this.focusedInput,
        onFocusChange: f => this.focusRangeChanged(f),
        displayFormat: "DD/MM/YYYY"
    };

    focusRangeChanged(e) {
        this.props.focusedInput = e;
        this.rcptDate.binding(this.props);
    }

    datesChanged(e) {
        this.props.focusedInput = null;
        this.props.startDate = e.startDate;
        this.props.endDate = e.endDate;
        this.startDate = e.startDate;
        this.endDate = e.endDate;

        // if (null != e.startDate) {
        this.dateForm.patchValue({ periodeDebut: e.startDate });
        // }

        if (
            null != e.startDate &&
            null != this.endDate &&
            this.startDate.isBefore(moment(this.endDate).add(-31, "days"))
        ) {
            const newDate = this.startDate.clone().add(31, "days");
            this.endDate = newDate;
            this.props.endDate = newDate;
            this.dateForm.patchValue({ periodeFin: newDate });
        } else {
            // if (null != e.endDate) {
            this.dateForm.patchValue({ periodeFin: e.endDate });
            // }
        }

        // if (null != e.startDate && null != e.endDate) {
        this.rcptDate.binding(this.props);
        // }
    }

    constructor(
        private router: Router,
        private fb: FormBuilder,
        private produitApi: ProduitService,
        private couponApi: CouponService,
        private transporteurApi: TransporteurService,
        private categorieApi: CategorieService,
        private emailApi: EmailService,
        private commandeApi: CommandeService,
        private commandeMag: MagasinsService,
        private userApi: UserService,
        private groupeApi: GroupeService,
        private moyenApi: MoyensService,
        private NgZone: NgZone,
        public store: StoreService
    ) {}

    onGroupeSelected($event) {
        //console.log("selected categories :", $event);

        if ($event.items.length > 0) {
            this.groupeDropdownPlaceholder = $event.items[0]["nom"];
            this.selectedGroupe = $event.items[0];
        } else {
            this.groupeDropdownPlaceholder = "Aucune sélection";
            this.selectedGroupe = null;
        }

        this.showGroupeDropdown = false;
    }

    ngOnInit() {
        // console.log(this.store);
        let memberUid = this.store.curSite.uids.filter(
            m => m.nom == "membership"
        );
        let id = memberUid[0]["distant_id"];
        this.store.memberId = id;
        // console.log('TODO : MODAL EXPORT PROD');
        // console.log('TODO : MODAL CREA PROMO');
        //console.log('TODO : MODAL EXPORT PROD');
        this.loadDateForm();
        this.loadUserForm();
    }

    loadUserForm() {
        this.userForm = this.fb.group({
            name: [
                "",
                [
                    Validators.required,
                    Validators.minLength(3),
                    Validators.maxLength(150)
                ]
            ],
            firstname: [
                "",
                [
                    Validators.required,
                    Validators.minLength(3),
                    Validators.maxLength(150)
                ]
            ],
            gender: [this.genreChoices[0], [Validators.required]],
            email: [
                "",
                [Validators.required, Validators.pattern(this.emailPattern)]
            ],
            telephone: [
                "",
                [Validators.required, Validators.pattern(this.telPattern)]
            ]
            //channels: ["", [Validators.required]],
            //groupes: [moment(), [Validators.required]],
        });

        this.genreChoix = this.genreChoices[0];
    }

    togglePromo(e) {
        // console.log("Toggle Promo");
        this.isPercentage = e;
    }

    ngAfterViewInit() {}

    loadDateForm() {
        this.dateForm = this.fb.group({
            periodeDebut: [moment(), [Validators.required]],
            periodeFin: [moment(), [Validators.required]]
        });
        this.startDate = moment();
        this.endDate = moment();
    }

    linkGroupeUser() {
        //console.log("link groupe...", this);
        //this.showGroupeDropdown = !this.showGroupeDropdown;

        if (this.isGroupeLoaded) {
            this.modalLinkGroupe.open();
        } else {
            this.groupeApi
                .apiMagasinsSlugGroupesGet(this.store.memberId.toString())
                .subscribe(e => {
                    this.groupes = e;
                    this.isGroupeLoaded = true;
                    this.modalLinkGroupe.open();
                });
        }
    }

    onDropdownGroupeDeleted($event) {
        this.groupeApi
            .apiMagasinsSlugGroupesGroupeDelete(
                this.store.memberId.toString(),
                $event.id.id
            )
            .subscribe(e => {
                this.onReRender.emit();
            });
    }

    confirmLinkGroupe() {
        let idItems = [];

        for (let i = 0; i < this.param.obj.ids.length; i++) {
            idItems.push({ id: this.param.obj.ids[i] });
        }

        this.userApi
            .apiMagasinsSlugUsersGroupeLinkGroupePut(
                idItems,
                this.store.memberId.toString(),
                this.selectedGroupe.id
            )
            .subscribe(e => {
                this.onReRender.emit();
                this.modalLinkGroupe.close();
            });
    }

    add() {
        if (this.domain.name == "Utilisateur") {
            //console.log('Add User');
            this.modalUser.open();
        } else {
            //console.log("new...", this.path.addPath);
            this.router.navigate([this.path.addPath]);
        }
    }

    addUser() {
        if (this.userForm.valid) {
            //console.log('Add User', this.userForm.value);
            let userData = { ...this.userForm.value };
            userData["channels"] = [2];
            this.userForm.reset();

            this.userApi
                .apiMagasinsSlugUsersPost(userData, this.store.memberId)
                .subscribe(e => {
                    //console.log(e);
                    this.onReRender.emit();
                    this.modalUser.close();
                    
                });
        } else {
            console.error("Add User", this.userForm.value);
        }
    }

    genreChanged($event) {
        this.userForm.controls["gender"].setValue($event, { onlySelf: true });
    }

    delete() {
        //console.log("delete...", this);
        this.isDeleting = true;

        let idItems = [];
        for (let i = 0; i < this.param.obj.ids.length; i++) {
            idItems.push({ id: this.param.obj.ids[i] });
        }

        switch (this.domain.name) {
            case "Utilisateur":
                this.userApi
                    .apiMagasinsSlugUsersMultisDeletesPost(
                        idItems,
                        this.store.memberId.toString()
                    )
                    .subscribe(e => {
                        // console.log(e);
                        this.isDeleting = false;
                        this.onReRender.emit();
                    });

                // this.moyenApi.apiMagasinsSlugMoyensMultisDeletesPost(idItems, this.store.paiementId.toString()).subscribe(e => {
                //     console.log(e);

                //     this.isDeleting = false;
                //     this.onReRender.emit();
                // });
                break;

            default:
                break;
        }
    }

    duplicate() {
        // console.log("clone...");
    }

    modify() {
        console.log(
            "edit...",
            this.param.obj["ids"][0],
            "/" + this.path.addPath,
            this.param.obj["ids"][0]
        );
        //setTimeout(() => this.router.navigate(['/'+this.path.addPath , this.param.obj["ids"][0]]), 200);
        this.NgZone.run(() =>
            this.router.navigate([
                "/utilisateur/utilisateurs/edit",
                this.param.obj["ids"][0]
            ])
        );
    }

    view() {
        // console.log("view...");
    }

    activate(status) {
        // console.log("activate...", status);
    }

    onDropdownDeleted($event) {
        // console.log("deleted", $event, $event.module);
        if ($event.module === "categorie") {
            this.categorieApi
                .apiMagasinsSlugCategoriesCategorieDelete(
                    this.store.shopId.toString(),
                    $event.id.id
                )
                .subscribe(e => {
                    this.onReRender.emit();
                });
        }
    }

    onShownModalExport() {
        this.props.endDate = this.endDate;
        this.props.startDate = this.startDate;
        this.rcptDate.binding(this.props);
    }
}



