import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";

import { FormGroup, Validators, FormBuilder, FormArray } from "@angular/forms";
import { StoreService } from "../../../shared/services/store.service";
import { User, UserService, GroupeService } from "../../../api-member";

@Component({
    selector: "app-user-form",
    templateUrl: "./edit.component.html",
    styleUrls: ["./edit.component.scss"]
})
export class EditComponent implements OnInit {
    isLoading = false;
    isUserLoaded = false;
    Id = null;
    userForm: FormGroup;
    genreChoices = [
        { name: "Mr", id: 0 },
        { name: "Mme", id: 1 },
        { name: "", id: 2 }
    ];
    genreChoix = {};
    emailPattern = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    telPattern = /(?:(?:\+|00)33[\s.-]{0,3}(?:\(0\)[\s.-]{0,3})?|0)[1-9](?:(?:[\s.-]?\d{2}){4}|\d{2}(?:[\s.-]?\d{3}){2})$/;
    user: User = {};

    showGroupeDropdown = false;
    selectedGroupe = null;
    groupeDropdownPlaceholder = "Sélectionner";
    groupes = [];
    isGroupeLoaded = false;

    selectedItems = {
        groupe: []
    };

    modules = [
        {
            name: "groupe",
            toggle: "groupeDropdownPlaceholder",
            field: "nom",
            dropdown: "showGroupeDropdown",
            props: "groupes",
            required: false
        }
    ];

    constructor(
        private userApi: UserService,
        private route: ActivatedRoute,
        private store: StoreService,
        private fb: FormBuilder,
        private groupeApi: GroupeService,
        private router: Router
    ) {}

    fillDropdownData(formArray, data, required) {
        this.clearFormArray(formArray);
        if (data.length == 0) {
            //formArray.setValue([{id:""}]);
        } else {
            data.forEach(element => {
                if (required) {
                    formArray.push(
                        this.fb.group(
                            { id: [element, Validators.required] },
                            Validators.required
                        )
                    );
                } else {
                    formArray.push(
                        this.fb.group({ id: [element, Validators.required] })
                    );
                }
            });
        }
    }

    reduceArr(iterable) {
        let arr = [];

        if (Array.isArray(iterable)) {
            iterable.forEach(element => {
                arr.push(element.id);
            });
        } else {
            console.log("Not iterable :", iterable);
        }

        return arr;
    }

    displayDropdownCss(field) {
        if (field) {
            return {
                "dropdown-error": field.invalid
            };
        } else {
            return null;
        }
    }

    toggleDropdown(name) {
        this[name] = !this[name];
    }

    clearFormArray = (formArray: FormArray) => {
        for (let i = formArray.length - 1; i >= 0; i--) {
            formArray.removeAt(i);
        }
    };

    onDropdownSelected($event) {
        let module = this.modules.filter(
            module => module.name == $event.module
        );
        let formArray = <FormArray>this.userForm.controls[module[0]["props"]];
        this.clearFormArray(formArray);

        if ($event.ids.length == 0) {
            //formArray.setValue([{id:""}]);
        } else {
            $event.ids.forEach(element => {
                if (module["required"]) {
                    formArray.push(
                        this.fb.group(
                            { id: [element, Validators.required] },
                            Validators.required
                        )
                    );
                } else {
                    formArray.push(
                        this.fb.group({ id: [element, Validators.required] })
                    );
                }
            });
        }

        let dropName = module[0]["dropdown"];
        this.dropdownPlaceholderText(module[0], $event.items);
        this[dropName] = false;
    }

    dropdownPlaceholderText(moduleName, items) {
        let toggle = moduleName["toggle"];
        let field = moduleName["field"];
        let field2 = moduleName["field2"];

        if (items.length == 1) {
            if (typeof field2 != "undefined") {
                this[toggle] =
                    '<img src="' +
                    items[0][field2] +
                    '" height="25px" /> ' +
                    items[0][field];
            } else {
                this[toggle] = items[0][field];
            }
        } else {
            this[toggle] = items.length + " selectionnés";
        }
    }

    ngOnInit() {
        this.groupeApi
            .apiMagasinsSlugGroupesGet(this.store.memberId.toString())
            .subscribe(gr => {
                this.groupes = gr;
                this.isGroupeLoaded = true;
                //console.log(gr);
                this.Id = this.route.snapshot.params.id;
                if (this.route.snapshot.params.id) {
                    this.userApi
                        .apiMagasinsSlugUsersUserGet(
                            this.store.memberId.toString(),
                            this.Id
                        )
                        .subscribe(e => {
                            this.user = { ...e };
                            //console.log("API user: ", e);
                            this.selectedItems.groupe = e.groupes;
                            this.createForm(e);
                            this.isUserLoaded = true;
                            this.dropdownPlaceholderText(
                                this.modules[0],
                                e["groupes"]
                            );
                            this.fillDropdownData(
                                <FormArray>this.userForm.controls["groupes"],
                                this.reduceArr(e["groupes"]),
                                false
                            );
                        });
                }
                // else {
                //     let e = {
                //         nom: "Mon Coupon",
                //         estMisEnAvant: true,
                //         typeReduction: 0,
                //         typeApplication: 0,
                //         // periodeDebut: moment().toString(),
                //         // periodeFin: moment().toString(),
                //         estTempsIllimite: true,
                //         sousTotalMin: 0,
                //         quantite: 0,
                //         estQuantiteIllimite: true,
                //         montantTtc: 0
                //     };
                //     this.createForm(e);
                //     this.isUserLoaded = true;
                // }
            });
    }

    byValue(item1, item2) {
        if (!item2) {
            return false;
        }
        return item1.id == item2.id;
    }

    showGender(g) {
        return g ? "Mr" : "Mme";
    }

    displayFieldCss(form, field: string) {
        if (!form.get(field)) {
            console.log("field not found :", field);
            return null;
        }

        return {
            "we-is-invalid": !form.get(field).valid
        };
    }

    genreChanged($event) {
        this.userForm.patchValue({ gender: $event });
    }

    formatDropdownPost(v, toReduce, firstOnly) {
        if (typeof v[toReduce] == "undefined") {
            //console.log(toReduce, "not existing ");
            return v;
        }
        let tempClone = [];

        //console.log("formatBeforePost :", v, toReduce );
        for (let i = 0; i < v[toReduce].length; i++) {
            tempClone.push(v[toReduce][i].id);
        }
        if (firstOnly) {
            v[toReduce] = tempClone[0];
        } else {
            v[toReduce] = tempClone;
        }

        //console.log("FormatBeforePost Return:", v);
        return v;
    }

    onDropdownGroupeDeleted($event) {
        let module = this.modules.filter(
            module => module.name == $event.module
        );
        let formArray = <FormArray>this.userForm.controls[module[0]["props"]];
        let valuesToKeep = [];

        for (let i = 0; i < formArray.value.length; i++) {
            if (formArray.value[i].id == $event.id.id) {
                formArray.removeAt(i);
            } else {
                //console.log(formArray.value[i].id, "!=", $event.id.id);
                valuesToKeep.push(formArray.value[i]);
            }
        }

        this.groupeApi
            .apiMagasinsSlugGroupesGroupeDelete(
                this.store.memberId.toString(),
                $event.id.id
            )
            .subscribe(e => {
                this.dropdownPlaceholderText(module[0], valuesToKeep);
            });
    }

    createForm(e) {
        let genderChx = this.genreChoices[2];

        for (let i = 0; i < this.genreChoices.length; i++) {
            if (this.genreChoices[i].name == e.gender) {
                genderChx = this.genreChoices[i];
            }
        }

        this.userForm = this.fb.group({
            name: [
                e.name,
                [
                    Validators.required,
                    Validators.minLength(2),
                    Validators.maxLength(150)
                ]
            ],
            firstname: [
                e.firstname,
                [Validators.minLength(2), Validators.maxLength(150)]
            ],
            gender: [genderChx, [Validators.required]],
            email: [
                e.email,
                [Validators.required, Validators.pattern(this.emailPattern)]
            ],
            telephone: [e.telephone, [Validators.pattern(this.telPattern)]],
            //channels: ["", [Validators.required]],
            groupes: this.fb.array([
                this.fb.group({ id: [null, Validators.required] }),
                [Validators.required]
            ])
        });

        this.genreChoix = genderChx;
    }

    onSubmit() {
        if (this.userForm.valid) {
            //console.log("submit", this.userForm.value);
            let userData = { ...this.userForm.value };
            userData["gender"] = userData["gender"].name;
            userData = this.formatDropdownPost(userData, "groupes", false);
            this.isLoading = true;
            this.userApi
                .apiMagasinsSlugUsersUserPut(
                    userData,
                    this.store.memberId,
                    this.Id
                )
                .subscribe(e => {
                    this.isLoading = false;
                    //console.log(e);
                });
        }
    }
}



