import { NgModule } from "@angular/core";
import { CommonModule } from "@angular/common";

import { UtilisateurRoutingModule } from "./utilisateur-routing.module";
import { MainComponent } from "./components/main/main.component";
import { AccueilComponent } from "./components/accueil/accueil.component";
import { SharedModule } from "../shared/shared.module";
import { ListComponent } from "./components/list/list.component";
import { PaginationComponent } from "./components/pagination/pagination.component";
import { EditComponent } from "./components/edit/edit.component";
import { NavigationSidebarComponent } from "./components/navigation-sidebar/navigation-sidebar.component";
import { ActionBarComponent} from "./components/actionbar/actionbar.component";
import { UserDropdownComponent} from "./components/dropdown/user-dropdown.component";
import { UserGroupFormComponent } from "./components/user-group-form/user-group-form.component";
import { ItemBarComponent } from "./components/itembar/itembar.component";

@NgModule({
    imports: [CommonModule, UtilisateurRoutingModule, SharedModule],
    declarations: [
        MainComponent,
        AccueilComponent,
        ListComponent,
        PaginationComponent,
        EditComponent,
        NavigationSidebarComponent,
        ActionBarComponent,
        UserDropdownComponent,
        UserGroupFormComponent,
        ItemBarComponent
    ],
    providers: []
})
export class UtilisateurModule {}



